progress {
  width: 100px;
  height: 30px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: white;
  /* IE10 호환성용 */
  color: red;
  border-radius: 5px;
}
progress::-webkit-progress-bar {
  background-color: #eee;
  border-radius: 5px;
}
progress::-webkit-progress-value {
  background-color: red;
  border-radius: 5px;
}

.univ_begin {
