}
.univ_begin .input_area .form_wrap .search_msg .list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 0;
  border-bottom: 1px solid gray;
}
.univ_begin .input_area .form_wrap .search_msg .list li:last-child {
  border-bottom: unset;
}
.univ_begin .input_area .form_wrap .search_msg .list li p {
  font-size: 13px;
}
.univ_begin .input_area .form_wrap .search_msg .list li button {
  font-size: 13px;
  border-radius: 5px;
  padding: 6px;
  color: #fff;
  background: #464646;
}
.univ_begin .input_area .form_wrap .btn_reset {
  display: block;
  padding: 3% 5%;
  margin: 0 auto;
  border-radius: 5px;
}
.univ_begin .input_area .form_wrap .btn_comp {
  display: block;
  width: 100%;
  padding: 5% 0%;
  margin-top: 3%;
  border-radius: 5px;
}
.univ_begin .input_area .form_wrap .notice_txt {
  margin-top: 3%;
  text-align: left;
  font-size: 12px;
  line-height: 14px;
  font-weight: normal;
  color: #575757;
}
.univ_begin .join_area {
  width: 100%;
}
.univ_begin .join_area .join_wrap {
  margin: 0 6.26%;
  padding: 7.6% 5.85%;
  border: 2px solid #ffa356;
  border-radius: 10px;
  background: #fff;
}
.univ_begin .join_area .join_wrap .join_tit {
  text-align: center;
  font-size: 16px;
}
.univ_begin .join_area .join_wrap .btn_wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 5%;
  padding: 5px 0 10px 0;
  border: 2px solid rgb(241, 224, 203);
  border-radius: 25px;
  background: #ffa356;
}
.univ_begin .join_area .join_wrap .btn_wrap .btn_join {
  transform: rotate(0deg);
  margin: 10% 20%;
  border-radius: 20px;
  font-size: 24px;
  background: transparent;
}
.univ_begin .join_area .join_wrap .btn_wrap .btn_join.clicked {
  animation: shake 0.8s ease-out 1;
}
@-webkit-keyframes shake {
  10% {
    transform: rotate(5deg);
  }
  20% {
    transform: rotate(-5deg);
  }
  60% {
    transform: rotate(10deg);
  }
  80% {
    transform: rotate(-10deg);
  }
}
@-moz-keyframes shake {
  10% {
    transform: rotate(5deg);
  }
  20% {
    transform: rotate(-5deg);
  }
  60% {
    transform: rotate(10deg);
  }
  80% {
    transform: rotate(-10deg);
  }
}
@keyframes shake {
  10% {
    transform: rotate(5deg);
  }
  20% {
    transform: rotate(-5deg);
  }
  60% {
    transform: rotate(10deg);
  }
  80% {
    transform: rotate(-10deg);
  }
}
.univ_begin .join_area .join_wrap .btn_wrap p {
  padding: 0 0 5px;
  font-size: 14px;
}
.univ_begin .join_area .join_wrap .name_wrap {
