}
.bluemarble .select_pop {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  justify-content: flex-end;
  width: 500px;
  height: 300px;
  background: blue;
  opacity: 0.9;
}
.bluemarble .select_pop.on {
  display: flex;
}
.bluemarble .select_pop .btn_close {
  position: relative;
  transform: translate(50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  background: white;
  cursor: pointer;
}
.bluemarble .select_pop .btn_close span {
  position: absolute;
  display: block;
  transform: rotate(-45deg);
  width: 20px;
  height: 2px;
  background: black;
}
.bluemarble .select_pop .btn_close span:last-child {
  transform: rotate(45deg);
}

