.animaition {
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
  transition: 1s ease-out;
}
.animaition svg {
  width: 110px;
  height: 110px;
}
.animaition .moving {
  stroke-dasharray: 400;
  stroke-dashoffset: 400;
  animation: ani1 2s linear alternate-reverse;
}
@-webkit-keyframes ani1 {
  0% {
    stroke-dashoffset: 400;
  }
  50% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -400;
  }
}
@-moz-keyframes ani1 {
  0% {
    stroke-dashoffset: 400;
  }
  50% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -400;
  }
}
@keyframes ani1 {
  0% {
    stroke-dashoffset: 400;
  }
  50% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -400;
  }
}
.animaition .ani01 {
  width: 100px;
  height: 100px;
  x: 5px;
  y: 5px;
  fill: none;
  stroke: #880E41;
  stroke-width: 5px;
}
.animaition.off {
  top: 0;
  left: 0;
  transform: unset;
}
.animaition.off svg {
  width: 40px;
  height: 40px;
}
.animaition.off .moving {
  stroke-dasharray: 130;
  stroke-dashoffset: 130;
  animation: ani2 3s infinite linear alternate-reverse;
}
.animaition.off .ani01 {
  width: 30px;
  height: 30px;
  x: 5px;
  y: 5px;
  fill: none;
  stroke: #880E41;
  stroke-width: 5px;
}
@-webkit-keyframes ani2 {
  0% {
    stroke-dashoffset: 130;
  }
  50% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -130;
  }
}
@-moz-keyframes ani2 {
  0% {
    stroke-dashoffset: 130;
  }
  50% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -130;
  }
}
@keyframes ani2 {
  0% {
    stroke-dashoffset: 130;
  }
  50% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -130;
  }
}

.animaition_bg {
